import React,{useState} from 'react';
import ToDoList from './ToDoList';

const App=()=>{
    const [Inputlist ,setInputlist] = useState("");
    const [Items,setItems] = useState([]);


    const InputField = (event) =>{
         setInputlist(event.target.value);

    };
    const Itemoflist =() =>{
    setItems((oldItems) =>{
  return[ ...oldItems, Inputlist];
    });
    setInputlist(" ");
    };

    const deleteItems=(id)=>{
        console.log("deleted");
setItems((oldItems)=>{
  return oldItems.filter((arrElt, index)=>{
return index!== id;
  });
});
    };

    return( 
        <>
        <div className = "main_div">
        <div className = "center_div">
        <br/>
        <h1> ToDo  List</h1>
        <br/>
        <input type= "text" 
         value = {Inputlist}
         onChange={InputField}   
          placeholder ="Add Items"></input> <button onClick={Itemoflist}> + </button>
          <hr/>
        <ol>
       {Items.map( (itemsval ,index) => {
             return <ToDoList text={itemsval} 
             key={index}
             id={index}
             onSelect={deleteItems}></ToDoList>;
           })}
        </ol>
        </div>

        </div>
        </>
    );
}

export default App;